.footer {
  width: 100%;
  margin: 100px 0 60px 0;
}

@media screen and (max-width: 680px) {
  .footer {
    margin: 70px 0 37px 0;
  }
}

.footer__copyright {
  line-height: 22px;
  font-size: 18px;
  font-weight: 400;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 680px) {
  .footer__copyright {
    line-height: 17px;
    font-size: 14px;
    margin: 0 0 0 19px;
  }
}
